<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card>
      <!--  -->
      <el-alert
        title=""
        type="info"
        effect="dark"
        :closable="false"
        show-icon=""
      >
      </el-alert>
      <!--  -->
      <el-row class="cat_opt">
        <el-col>
          <span>:</span>

          <!--  -->
          <el-cascader
            :options="cateList"
            :props="cateProps"
            v-model="selectedOptions2"
            @change="parentCateChange"
            clearable
            class="interval"
          >
          </el-cascader>
        </el-col>
        <el-col></el-col>
      </el-row>
      <!-- tab  -->
      <el-tabs v-model="activeName" @tab-click="handleTabClick">
        <!--  -->
        <el-tab-pane label="" name="many">
          <el-button
            type="primary"
            :disabled="isBtnDisabled"
            @click="activeNameVisible = true"
            ></el-button
          >
          <el-table :data="manyList" height="250" border style="width: 100%">
            <!--  -->
            <el-table-column type="expand">
              <template slot-scope="scope">
                <el-tag
                  type=""
                  v-for="(item, index) in scope.row.attr_vals"
                  :key="index"
                  closable
                  class="interval"
                  @close="handleClose(index,scope.row)"
                >
                  {{ item }}
                </el-tag>
                <!--  tag -->
                <el-input
                  v-if="scope.row.inputVisible"
                  v-model="scope.row.inputValue"
                  ref="saveTagInput"
                  size="small"
                  @keyup.enter.native="handleInputConfirm(scope.row)"
                  @blur="handleInputConfirm(scope.row)"
                  class="input-new-tag"
                >
                </el-input>
                <el-button
                  v-else
                  class="button-new-tag"
                  size="small"
                  @click="showInput(scope.row)"
                  > </el-button
                >
              </template>
            </el-table-column>
            <el-table-column label="@" type="index"> </el-table-column>
            <el-table-column label="" prop="attr_name"> </el-table-column>
            <el-table-column label="">
              <template slot-scope="scope">
                <el-tooltip
                  effect="dark"
                  content=""
                  placement="top"
                  :enterable="false"
                >
                  <el-button
                    type="primary"
                    icon="el-icon-edit"
                    circle
                    @click="showEditDialog(scope.row.attr_id)"
                  ></el-button>
                </el-tooltip>
                <el-tooltip
                  effect="dark"
                  content=""
                  placement="top"
                  :enterable="false"
                >
                  <el-button
                    type="danger"
                    icon="el-icon-delete"
                    circle
                    @click="delDialog(scope.row.attr_id)"
                  ></el-button
                ></el-tooltip>
              </template>
            </el-table-column>
          </el-table>
        </el-tab-pane>
        <!--  -->
        <el-tab-pane label="" name="only">
          <el-button
            type="primary"
            :disabled="isBtnDisabled"
            @click="activeNameVisible = true"
            ></el-button
          >
          <el-table :data="onlyList" height="250" border style="width: 100%">
            <!--  -->
                <el-table-column type="expand">
              <template slot-scope="scope">
                <el-tag
                  type=""
                  v-for="(item, index) in scope.row.attr_vals"
                  :key="index"
                  closable
                  class="interval"
                  @close="handleClose(index,scope.row)"
                >
                  {{ item }}
                </el-tag>
                <!--  tag -->
                <el-input
                  v-if="scope.row.inputVisible"
                  v-model="scope.row.inputValue"
                  ref="saveTagInput"
                  size="small"
                  @keyup.enter.native="handleInputConfirm(scope.row)"
                  @blur="handleInputConfirm(scope.row)"
                  class="input-new-tag"
                >
                </el-input>
                <el-button
                  v-else
                  class="button-new-tag"
                  size="small"
                  @click="showInput(scope.row)"
                  > </el-button
                >
              </template>
            </el-table-column>
            <el-table-column label="@" type="index"> </el-table-column>
            <el-table-column label="" prop="attr_name"> </el-table-column>
            <el-table-column label="">
              <template slot-scope="scope">
                <el-tooltip
                  effect="dark"
                  content=""
                  placement="top"
                  :enterable="false"
                >
                  <el-button
                    type="primary"
                    icon="el-icon-edit"
                    circle
                    @click="showEditDialog(scope.row.attr_id)"
                  ></el-button>
                </el-tooltip>
                <el-tooltip
                  effect="dark"
                  content=""
                  placement="top"
                  :enterable="false"
                >
                  <el-button
                    type="danger"
                    icon="el-icon-delete"
                    circle
                    @click="delDialog(scope.row.attr_id)"
                  ></el-button
                ></el-tooltip>
              </template>
            </el-table-column>
          </el-table>
        </el-tab-pane>
      </el-tabs>
    </el-card>
    <!-- --------- -->
    <!--  -->
    <el-dialog
      :title="titleDlog"
      :visible.sync="activeNameVisible"
      width="50%"
      @close="addRuleFormClose"
    >
      <el-form
        :model="addRuleForm"
        :rules="addRules"
        ref="addRuleFormRef"
        label-width="100px"
      >
        <el-form-item :label="titleDlog" prop="attr_name">
          <el-input v-model="addRuleForm.attr_name"></el-input> </el-form-item
      ></el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="activeNameVisible = false"> </el-button>
        <el-button type="primary" @click="addRuleFormFn()"> </el-button>
      </span>
    </el-dialog>
    <!-- ----------- -->

    <!--  -->
    <el-dialog
      title=""
      :visible.sync="reviseNameVisible"
      width="50%"
      @close="reviseFormClose"
    >
      <el-form
        :model="reviseRuleForm"
        :rules="reviseRules"
        ref="reviseRuleFormRef"
        label-width="100px"
      >
        <el-form-item label=":" prop="attr_name">
          <el-input
            v-model="reviseRuleForm.attr_name"
          ></el-input> </el-form-item
      ></el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="reviseNameVisible = false"> </el-button>
        <el-button type="primary" @click="reviseParma"> </el-button>
      </span>
    </el-dialog>
    <!--  -->
  </div>
</template>

<script>
export default {
  name: 'goodsParams',
  created () {
    this.getCateList()
  },
  data () {
    return {
      cateList: [],
      manyList: [],
      onlyList: [],
      addRuleForm: {
        attr_name: ''
      },
      addRules: {
        attr_name: [{ required: true, message: '', trigger: 'blur' }]
      },
      reviseRules: {
        attr_name: [{ required: true, message: '', trigger: 'blur' }]
      },
      activeNameVisible: false,
      reviseNameVisible: false,
      inputVisible: false,
      inputValue: '',
      reviseRuleForm: {
        attr_name: '',
        attr_id: ''
      },
      selectedOptions2: '',
      //   
      cateProps: {
        expandTrigger: 'hover',
        value: 'cat_id',
        label: 'cat_name',
        children: 'children'
        // checkStrictly: true
      },
      //   
      activeName: 'many'
    }
  },
  methods: {
    async getCateList () {
      const { data: res } = await this.$axios.get('categories')
      if (res.meta.status !== 200) {
        return this.$message.error(res.meta.msg)
      }
      this.cateList = res.data
      //   console.log(res.data)
      return this.$message.success(res.meta.msg)
    },
    // 
    parentCateChange () {
      //   console.log(this.selectedOptions2)
      this.getParamsData()
    },
    // 
    async getParamsData () {
      if (this.selectedOptions2.length !== 3) {
        this.selectedOptions2 = []
        this.manyList = []
        this.onlyList = []
        return
      }
      const { data: res } = await this.$axios.get(
        `categories/${this.cateId}/attributes`,
        {
          params: {
            sel: this.activeName
          }
        }
      )
      if (res.meta.status !== 200) {
        return this.$message.error(res.meta.msg)
      }
      res.data.forEach((item) => {
        item.attr_vals = item.attr_vals ? item.attr_vals.split(',') : []
        // 
        item.inputVisible = false
        // 
        item.inputValue = ''
      })

      if (this.activeName === 'many') {
        this.manyList = res.data
      } else {
        this.onlyList = res.data
      }
      console.log(res.data)
      return this.$message.success(res.meta.msg)
    },
    // 
    handleTabClick () {
      this.getParamsData()
    },
    // 
    addRuleFormClose () {
      this.$refs.addRuleFormRef.resetFields()
    },
    // 
    addRuleFormFn () {
      this.$refs.addRuleFormRef.validate(async (v) => {
        if (!v) {
          return console.log('')
        }
        const { data: res } = await this.$axios.post(
          `categories/${this.cateId}/attributes`,
          {
            attr_name: this.addRuleForm.attr_name,
            attr_sel: this.activeName
          }
        )
        if (res.meta.status !== 201) {
          return this.$message.error(res.meta.msg)
        }
        this.getParamsData()
        this.activeNameVisible = false
        return this.$message.success(res.meta.msg)
      })
    },
    //  
    async showEditDialog (id) {
      const { data: res } = await this.$axios.get(
        `categories/${this.cateId}/attributes/${id}`,
        {
          params: {
            attr_sel: this.activeName
          }
        }
      )
      if (res.meta.status !== 200) {
        return this.$message.error(res.meta.msg)
      }
      this.reviseRuleForm = res.data
      this.reviseNameVisible = true
      return this.$message.success(res.meta.msg)
    },
    reviseParma () {
      this.$refs.reviseRuleFormRef.validate(async (v) => {
        if (!v) {
          return this.$message.error('')
        }
        const { data: res } = await this.$axios.put(
          `categories/${this.cateId}/attributes/${this.reviseRuleForm.attr_id}`,
          {
            attr_name: this.reviseRuleForm.attr_name,
            attr_sel: this.activeName
          }
        )
        if (res.meta.status !== 200) {
          return this.$message.error(res.meta.msg)
        }
        this.getParamsData()
        this.reviseNameVisible = false
        return this.$message.success(res.meta.msg)
      })
    },
    reviseFormClose () {
      this.$refs.reviseRuleFormRef.resetFields()
    },
    // 
    async delDialog (id) {
      const confirmRes = await this.$confirm(
        ', ?',
        '',
        {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }
      ).catch((err) => err)
      if (confirmRes !== 'confirm') {
        return this.$message.error('')
      }
      const { data: res } = await this.$axios.delete(
        `categories/${this.cateId}/attributes/${id}`
      )
      if (res.meta.status !== 200) {
        return this.$message.error(res.meta.msg)
      }
      this.getParamsData()
      return this.$message.success(res.meta.msg)
    },
    // tag
    async handleInputConfirm (id) {
      if (id.inputValue.trim().length === 0) {
        id.inputValue = ''
        id.inputVisible = false
        return this.$message.error('')
      }
      id.attr_vals.push(id.inputValue.trim())
      id.inputValue = ''
      id.inputVisible = false
      //   
      const { data: res } = await this.$axios.put(`categories/${this.cateId}/attributes/${id.attr_id}`, {
        attr_name: id.attr_name, attr_sel: id.attr_sel, attr_vals: id.attr_vals.join(',')
      })
      if (res.meta.status !== 200) {
        return this.$message.error(res.meta.msg)
      }
      return this.$message.success(res.meta.msg)
    },
    // 
    handleClose (index, row) {
      row.attr_vals.splice(index, 1)
      this.saveAttribute(row)
    },
    // 
    async saveAttribute (id) {
      const { data: res } = await this.$axios.put(`categories/${this.cateId}/attributes/${id.attr_id}`, {
        attr_name: id.attr_name, attr_sel: id.attr_sel, attr_vals: id.attr_vals.join(',')
      })
      if (res.meta.status !== 200) {
        return this.$message.error(res.meta.msg)
      }
      return this.$message.success(res.meta.msg)
    },
    // tag
    showInput (id) {
      id.inputVisible = true
      this.$nextTick(_ => {
        this.$refs.saveTagInput.$refs.input.focus()
      })
    }
  },
  computed: {
    isBtnDisabled () {
      if (this.selectedOptions2.length !== 3) {
        return true
      }
      return false
    },
    // id
    cateId () {
      if (this.selectedOptions2.length === 3) {
        return this.selectedOptions2[2]
      }
      return null
    },
    titleDlog () {
      if (this.activeName === 'many') {
        return ''
      } else {
        return ''
      }
    }
  }
}
</script>

<style lang="less" scoped>
.cat_opt {
  margin: 15px 0;
}
.interval {
  margin: 3px 5px;
}
.input-new-tag {
    width: 90px;
    margin-left: 10px;
    vertical-align: bottom;
  }
    .button-new-tag {
    margin-left: 10px;
    height: 32px;
    line-height: 30px;
    padding-top: 0;
    padding-bottom: 0;
  }
</style>
