<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card>
      <el-row :gutter="20">
        <el-col :span="7">
          <el-input
            placeholder=""
            v-model="queryInfo.query"
            clearable
            @clear="getUserList"
          >
            <el-button
              slot="append"
              icon="el-icon-search"
              @click="getUserList"
            ></el-button> </el-input
        ></el-col>
        <el-col :span="4"
          ><el-button type="primary" plain @click="addDialogVisible = true"
            ></el-button
          ></el-col
        >
      </el-row>
      <!--  -->
      <el-table :data="userList" style="width: 100%" border stripe>
        <!--  -->
        <el-table-column type="index" label="#"></el-table-column>
        <el-table-column prop="username" label=""> </el-table-column>
        <el-table-column prop="mobile" label=""> </el-table-column>
        <el-table-column prop="email" label=""> </el-table-column>
        <el-table-column prop="role_name" label=""> </el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <!-- {{scope.row}} -->
            <el-switch
              v-model="scope.row.mg_state"
              @change="userStateChanged(scope.row)"
            >
            </el-switch>
          </template>
        </el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <el-tooltip
              effect="dark"
              content=""
              placement="top"
              :enterable="false"
            >
              <el-button
                type="primary"
                icon="el-icon-edit"
                circle
                @click="showEditDialog(scope.row.id)"
              ></el-button
            ></el-tooltip>

            <el-tooltip
              effect="dark"
              content=""
              placement="top"
              :enterable="false"
              ><el-button
                type="danger"
                icon="el-icon-delete"
                circle
                @click="removeUserById(scope.row.id)"
              ></el-button
            ></el-tooltip>

            <el-tooltip
              effect="dark"
              content=""
              placement="top"
              :enterable="false"
            >
              <el-button
                type="warning"
                icon="el-icon-star-off"
                circle
                @click="distributeUser(scope.row)"
              ></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>
      <!-- / -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[2, 5, 10, 50]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      >
      </el-pagination>
    </el-card>
    <!-- - -->
    <el-dialog
      title=""
      :visible.sync="addDialogVisible"
      width="50%"
      @close="addDialogClose"
    >
      <el-form
        :model="addFrom"
        :rules="addFromRules"
        ref="addFromRef"
        label-width="100px"
      >
        <el-form-item label="" prop="username">
          <el-input v-model="addFrom.username"></el-input>
        </el-form-item>
        <el-form-item label="" prop="password">
          <el-input v-model="addFrom.password"></el-input>
        </el-form-item>
        <el-form-item label="" prop="email">
          <el-input v-model="addFrom.email"></el-input>
        </el-form-item>
        <el-form-item label="" prop="mobile">
          <el-input v-model="addFrom.mobile"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <!--  -->
        <el-button @click="addDialogVisible = false"> </el-button>
        <el-button type="primary" @click="addUser"> </el-button>
      </span>
    </el-dialog>
    <!-- ----------------------------------------- -->
    <!--  -->
    <el-dialog
      title=""
      :visible.sync="showEditDialogVisible"
      width="30%"
      @close="editFromClose"
    >
      <!-- form -->
      <el-form
        :model="editFrom"
        :rules="addFromRules"
        label-width="80px"
        ref="editFromRef"
      >
        <el-form-item label="" prop="">
          <el-input :disabled="true" v-model="editFrom.username"></el-input>
        </el-form-item>
        <el-form-item label="" prop="mobile">
          <el-input v-model="editFrom.mobile"></el-input>
        </el-form-item>
        <el-form-item label="" prop="email">
          <el-input v-model="editFrom.email"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="showEditDialogVisible = false"> </el-button>
        <el-button type="primary" @click="editUserInfo(editFrom.id)"
          > </el-button
        >
      </span>
    </el-dialog>
    <!-- ------------------ -->
    <!--  -->
    <el-dialog
      title=""
      :visible.sync="distributeDialogVisible"
      width="50%"
      @close="distributeDialogClose"
    >
      <div>
        <p>:{{ userInfo.username }}</p>
        <p>:{{ userInfo.role_name }}</p>
      </div>
      <el-select v-model="rolesListValue" placeholder="">
        <el-option
          v-for="item in this.rolesList"
          :key="item.id"
          :label="item.roleName"
          :value="item.roleName"
        >
        </el-option>
      </el-select>
      <span slot="footer" class="dialog-footer">
        <el-button @click="distributeDialogVisible = false"> </el-button>
        <el-button type="primary" @click="saveRoleInfo"> </el-button>
      </span>
    </el-dialog>
    <!-- -------- -->
  </div>
</template>

<script>
export default {
  name: 'Users-',
  data () {
    const checkEmail = (rule, value, cb) => {
      // 
      const regEmail = /^([a-zA-Z0-9_-])+@([a-zA-Z0-9_-])+(\.[a-zA-Z0-9_-])+/
      if (regEmail.test(value)) {
        return cb()
      }
      cb(new Error(''))
    }
    const checkMobile = (rule, value, cb) => {
      // 
      const regMobile =
        /^(0|86|17951)?(13[0-9]|15[0123456789]|17[678]|18[0-9]|14[57])[0-9]{8}$/
      if (regMobile.test(value)) {
        return cb()
      }
      cb(new Error(''))
    }
    return {
      queryInfo: {
        query: '',
        pagenum: 1,
        pagesize: 2
      },
      rolesListValue: '',
      userInfo: {},
      rolesList: [],
      distributeDialogVisible: false,
      userList: [],
      total: 0,
      addDialogVisible: false,
      showEditDialogVisible: false,
      delVisible: false,
      editFrom: {},
      addFrom: {
        username: '',
        password: '',
        email: '',
        mobile: ''
      },
      addFromRules: {
        // 
        username: [
          { required: true, message: '', trigger: 'blur' },
          {
            min: 3,
            max: 10,
            message: ' 3  10 ',
            trigger: 'blur'
          }
        ],
        password: [
          { required: true, message: '', trigger: 'blur' },
          { min: 6, max: 21, message: ' 6  24', trigger: 'blur' }
        ],
        email: [
          { required: true, message: '', trigger: 'blur' },
          { validator: checkEmail, trigger: 'blur' }
        ],
        mobile: [
          { required: true, message: '', trigger: 'blur' },
          { validator: checkMobile, trigger: 'blur' }
        ]
      }
    }
  },
  created () {
    this.getUserList()
  },
  methods: {
    async getUserList () {
      const { data: res } = await this.$axios.get('users', {
        params: this.queryInfo
      })
      //   console.log(res)
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.userList = res.data.users
      this.total = res.data.total
    },
    handleSizeChange (newS) {
      // 
      this.queryInfo.pagesize = newS
      this.getUserList()
    },
    handleCurrentChange (newPG) {
      // 
      this.queryInfo.pagenum = newPG
      this.getUserList()
    },
    async userStateChanged (infoUser) {
      // switch
      // console.log(infoUser)
      const { data: res } = await this.$axios.put(
        `users/${infoUser.id}/state/${infoUser.mg_state}`
      )
      // console.log(res)
      if (res.meta.status !== 200) {
        infoUser.mg_state = !infoUser.mg_state
        return this.$message.error('!')
      }
      return this.$message.success('')
    },
    addDialogClose () {
      // ,
      this.$refs.addFromRef.resetFields()
    },
    editFromClose () {
      this.$refs.editFromRef.resetFields()
    },
    addUser () {
      // 
      // 
      this.$refs.addFromRef.validate(async (v) => {
        // console.log(v)
        if (!v) {
          return this.$message.error('')
        }
        const { data: res } = await this.$axios.post('users', this.addFrom)
        if (res.meta.status !== 201) {
          return this.$message.error(res.meta.msg)
        }
        // console.log(res)
        this.addDialogVisible = false
        this.getUserList()
        return this.$message.success('')
      })
    },
    async showEditDialog (id) {
      const { data: res } = await this.$axios.get('users/' + id)
      if (res.meta.status !== 200) {
        return this.$message.error(res.meta.msg)
      }
      this.editFrom = res.data
      this.showEditDialogVisible = true
    },
    editUserInfo (id) {
      // this.$refs.editFromRef.validate 
      this.$refs.editFromRef.validate(async (v) => {
        if (!v) {
          return this.$message.error('')
        }
        const { data: res } = await this.$axios.put(
          'users/' + id,
          this.editFrom
        )
        // console.log(res)
        if (res.meta.status !== 200) {
          return this.$message.error(res.meta.msg)
        }
        this.getUserList()
        this.showEditDialogVisible = false
        this.$message.success(res.meta.msg)
      })
    },
    async removeUserById (id) {
      // 
      const res = await this.$confirm(
        ', ?',
        '!',
        {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }
      ).catch((err) => err)
      // console.log(res)
      //   confirm
      //  cancel 
      if (res !== 'confirm') {
        return this.$message.info('')
      }
      const { data: resS } = await this.$axios.delete('users/' + id)
      if (resS.meta.status !== 200) {
        return this.$message.error(resS.meta.msg)
      }
      this.getUserList()
      this.$message.success(resS.meta.msg)
    },
    async distributeUser (userInfo) {
      this.userInfo = userInfo
      const { data: res } = await this.$axios.get('roles')
      // console.log(res)
      if (res.meta.status !== 200) {
        return this.$message.error(res.meta.msg)
      }
      this.rolesList = res.data
      this.distributeDialogVisible = true
      return this.$message.success(res.meta.msg)
    },
    async saveRoleInfo () {
      if (!this.rolesListValue) {
        return this.$message.error('')
      }
      const { data: res } = await this.$axios.put(
        `users/${this.userInfo.id}/role`,
        {
          rid: this.rolesListValue
        }
      )
      console.log(this.rolesListValue)
      if (res.meta.status !== 200) {
        return this.$message.error(res.meta.msg)
      }
      this.distributeDialogVisible = false
      this.getUserList()
      return this.$message.success(res.meta.msg)
    },
    distributeDialogClose () {
      this.rolesListValue = ''
      this.userInfo = {}
    }
  }
}
</script>

<style lang="less" scoped></style>
